@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
*{margin:0}body{margin:0;font-family:'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-y:scroll;background-color:black;color:#FFF;--lineBreakColor: #555;--myBlue: #32387f;--myBrightBlue: #52589f;--movieHeight: 17em}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}input{border:0;outline:0}a{text-decoration:none;color:unset}ul{display:-webkit-flex;display:flex;list-style:none;grid-column:2/3;padding:0}.overlay{width:100%;height:100%;display:-webkit-flex;display:flex;position:absolute;left:0;top:0;z-index:1;background:linear-gradient(to top, #000 0%, transparent 60%);-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:stretch;align-items:stretch;cursor:pointer;color:#FFF}.movieGenres{width:100%;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;grid-column:2 / 3;grid-row-start:3;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.genreItem{background-color:var(--myBlue);color:#FFF;font-size:0.7em;padding:0.2em;border-radius:1em;margin:0 0.1em}.spreadHoriz{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:baseline;align-items:baseline}.underLine{border-bottom:1px solid var(--lineBreakColor)}.clickable{cursor:pointer}.listTitle{grid-column:2/3;margin-bottom:1em;text-align:left;font-size:1.4em;border-bottom:1px solid var(--lineBreakColor)}.dropDownArrow{width:1em;vertical-align:middle;-webkit-filter:invert(100%);filter:invert(100%);-webkit-transform:rotate(0deg);transform:rotate(0deg);transition:-webkit-transform 0.4s;transition:transform 0.4s;transition:transform 0.4s, -webkit-transform 0.4s}.dropDownArrow.rotated{-webkit-transform:rotate(180deg);transform:rotate(180deg)}@media only screen and (max-width: 600px){.MoviesList{grid-template-columns:repeat(1, 1fr)}}@media only screen and (min-width: 600px){.MoviesList{grid-template-columns:repeat(2, 1fr)}}@media only screen and (min-width: 768px){.MoviesList{grid-template-columns:repeat(3, 1fr)}}@media only screen and (min-width: 992px){.MoviesList{grid-template-columns:repeat(3, 1fr)}}@media only screen and (min-width: 1200px){.MoviesList{grid-template-columns:repeat(4, 1fr)}}

.Movie{position:relative;width:182px;height:var(--movieHeight);overflow:hidden;background-color:#000}.Movie img{height:100%;position:absolute;z-index:0;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.Movie .missingPoster{-webkit-filter:invert(100%);filter:invert(100%);height:90%;-webkit-transform:translate(-55%, -50%);transform:translate(-55%, -50%)}.Movie .movieTitle{width:100%;text-align:center;font-size:1em}.Movie .overlay{box-sizing:border-box;visibility:hidden;background:linear-gradient(to top, #000 0%, transparent 100%);padding-bottom:1em}.Movie:hover .overlay{visibility:visible}.Movie:hover{box-shadow:0 0 1.4em 0.0em #444;-webkit-transform:scale(1.1);transform:scale(1.1)}

.MoviesList{display:grid;justify-items:center;grid-row-gap:1em;grid-template-rows:auto}

.Welcome{display:grid;grid-template-columns:1fr 70% 1fr;grid-template-rows:auto;margin-top:2em}.Welcome .listTitle{margin-top:0.6em}.Welcome .MoviesList{grid-column:2/3;-webkit-justify-content:space-between;justify-content:space-between;overflow-y:hidden;grid-row-gap:0}.Welcome .MoviesList.collapsed{grid-auto-rows:0}.Welcome .MoviesList .Movie{margin:1em 0}

.Filter{grid-column:1/2;grid-row:2/3;width:90%;height:-webkit-min-content;height:min-content;justify-self:center;background-color:#222;border-radius:1em;padding:1em 0;font-size:0.8em}.Filter h2{border-bottom:3px solid var(--myBlue);width:90%;padding:0;margin:0 auto 1em}.Filter h3{margin:1em 0 0.5em}.Filter .movieGenres{display:block;width:70%;margin:0 auto;white-space:normal;text-align:left}.Filter .movieGenres .genreItem{display:inline-block;font-size:0.9em;margin-bottom:0.2em;background-color:#444;cursor:pointer}.Filter .movieGenres .genreItem.selected{background-color:var(--myBlue)}

.DualSlider{width:100%}.DualSlider p{width:60%;margin:0 auto}.DualSlider p input{width:100%;margin:0 auto}.DualSlider p span{white-space:nowrap;display:inline-block}

.NavBar{grid-row:3/4;grid-column:2/3;display:-webkit-flex;display:flex;width:-webkit-min-content;width:min-content;justify-self:center;margin:1em 0;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}.NavBar button{width:2em;height:2em;border:0;border-radius:0.4em;margin:0.4em;background-image:url(data:image/png;base64,);background-position:center;background-size:100% 100%;-webkit-transform:rotate(270deg);transform:rotate(270deg);cursor:pointer}.NavBar button.flipped{-webkit-transform:rotate(90deg);transform:rotate(90deg)}

.SearchResults{display:grid;grid-template-columns:1.4fr 70% 0.6fr;grid-template-rows:-webkit-min-content auto auto;grid-template-rows:min-content auto auto;grid-column-gap:1em;margin-top:1em}.SearchResults .listTitle{grid-column:2/3;border-bottom:1px solid #AAA;margin-bottom:1em;text-align:left;font-size:1.1em}.SearchResults .listTitle small{font-size:1em}.SearchResults .MoviesList{grid-column:2/3;grid-row-start:2}

.posterContainer{grid-column:1/4;display:grid;position:relative;grid-template-columns:1fr 70% 1fr;text-align:center;background-color:#000}.Poster{position:relative;grid-column:2/3;margin:0 auto}.Poster .overlayText{text-align:left;margin:0 0 1em 1em}.Poster .overlay{grid-column:2/3}.Poster .overlayText h1{font-size:2.4em;letter-spacing:0.1em}.Poster .posterImg{width:100%;z-index:-1}.Poster .trailerFrame{position:absolute;top:0;left:0;z-index:0;width:100%;height:100%}#closeTrailer{grid-column:1/2;grid-row:1/2;align-self:center;justify-self:center;cursor:pointer;-webkit-filter:invert(100%);filter:invert(100%)}#openTrailer{margin-left:1em}.playIcon{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:5rem;height:5rem;visibility:hidden}.Poster:hover .playIcon{visibility:visible}

.Review{margin:1em 0;padding:1em;background-color:#222;font-size:0.85em;border-left:3px solid var(--myBlue)}

.ReviewForm input[type='submit']{border-radius:1em;background-color:#FFF;color:#000}.ReviewForm input[type='submit'][disabled]{background-color:#555}

.MovieDetails{display:grid;grid-template-columns:1fr 60% 1fr;grid-template-rows:repeat(3, auto);overflow:hidden;background-color:#111}.MovieDetails h2{margin:0.5em 0 0.5em}.MovieDetails .detailsText{text-align:left;grid-row:2/3;grid-column:2/3}.MovieDetails .actorsList{--actorWidth: 90px;display:grid;grid-template-columns:repeat(auto-fill, calc(var(--actorWidth) + 1em));grid-template-rows:auto}.MovieDetails .actorsList.collapsed{grid-auto-rows:0;overflow-y:hidden}.MovieDetails .actorItem{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;width:var(--actorWidth);margin:0.5em 0.5em 0}.MovieDetails .actorItem img{width:100%}.MovieDetails .actorItem .missingProfile{margin-top:1em;-webkit-filter:invert(150%);filter:invert(150%)}.MovieDetails .userReviewText{display:block;width:90%;resize:none;box-sizing:border-box;outline:0;margin:0 0}

.Discover{display:grid;grid-template-columns:1.4fr 70% 0.6fr;grid-template-rows:-webkit-min-content auto auto;grid-template-rows:min-content auto auto;grid-column-gap:1em;margin-top:1em}.Discover .MoviesList{grid-column:2/3;grid-row-start:2}.Discover .pageNav{grid-row:3/4;grid-column:2/3;margin:1em 0}

.UserReviews{width:70%;margin:2em auto 0}.UserReviews .reviewsList{width:-webkit-max-content;width:max-content;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:1em auto}.UserReviews .Movie{-webkit-flex-shrink:0;flex-shrink:0;-webkit-transform:scale(0.9);transform:scale(0.9);margin:0}.UserReviews .userReview{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-start;justify-content:flex-start;background-color:#333;margin-bottom:1em}.UserReviews .userReview h3{margin-left:2em}.UserReviews big{font-size:2.5em;margin:0.5em}

.SearchBar {
    grid-column: 3/4;
    height: 1.2em;
    width: 50%;
    justify-self: center;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    align-self: center;
    background-color: #FFF;
    white-space: nowrap;
    border-radius: 1em;
    overflow: hidden;
    padding: 0.3em;
}

.SearchBar .searchIcon {
    height: 90%;
}

.searchIcon img {
    height: 100%;
}

.Header{display:grid;grid-template-columns:0.3fr 0.7fr 1fr 1fr;margin:0;padding:0.2em 1em;background-color:#222;grid-column-gap:0.5em}.Header h1{color:white;text-align:left}.Header .navBar{grid-column:2/3;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}.Header .navBar li{box-sizing:border-box}.Header .navBar li:hover{border-bottom:3px solid var(--myBrightBlue)}

.Footer{background-color:#333;padding:3em 4em 1em;margin:5em 0 0;font-size:0.8em;text-align:left}.Footer .logo{height:1em}

.App{text-align:center}.App .Header+*{min-height:90vh}

