.Discover {
	display: grid;
	grid-template-columns: 1.4fr 70% 0.6fr;
	grid-template-rows: min-content auto auto;
	grid-column-gap: 1em;
	margin-top: 1em;
	.MoviesList {
		grid-column: 2/3;
		grid-row-start: 2;
	}

	.pageNav {
		grid-row: 3/4;
		grid-column: 2/3;
		margin: 1em 0;
	}
}
